import './App.css';
import {Route, Switch, Link} from "wouter";
import About from "./components/About";
import Home from "./components/home";
import {Helmet} from "react-helmet";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Lottie from 'react-lottie';
import animationData from './lotties/lf20_66vracut';
import Loadable from 'react-loadable';
function App() {


    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    };
    const Loader=x => Loadable({
            loader: x,
            loading: () => {
                return (    <div>
                    <Lottie
                        options={defaultOptions}
                        height={400}
                        width={400}
                    />
                </div>);
            }
        }
    );
    return (
        <div className="App">
            <Helmet>
                <meta charSet="utf-8" />
                <title>Marc tio guapo</title>
                <link rel="canonical" href="http://mysite.com/example" />
            </Helmet>

            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Link to="/about/marc"><Nav.Link href="#home">about</Nav.Link></Link>
                            <Nav.Link href="#link">Link</Nav.Link>
                            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.2">
                                    Another action
                                </NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.4">
                                    Separated link
                                </NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <div> hola marc</div>
            <Switch>
                <Route path="/" component={Home}/>
                <Route path="/about/:name" component={About}/>
                <Route path="/:rest*">
                    {(params) => `404, Sorry the page ${params.rest} does not exist!`}
                </Route>
            </Switch>
        </div>
);
}

export default App;
