import React from 'react';
import {Link, useRoute} from 'wouter';
import Lottie from 'react-lottie';
import animationData from '../lotties/lf20_66vracut';

export default function About({params}) {

    const {name} = params;
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    };
    return (

        <div>
            <h1>Este es el componente about, hola {name}</h1>
            <Link href="/"><a style={{color: 'red', background: '#000000'}}>Home</a></Link>
            <Lottie
                options={defaultOptions}
                height={400}
                width={400}
            />
        </div>
    );
}